import { LeftCircleOutlined, RightCircleOutlined } from "@ant-design/icons";
import React, { useRef } from "react";
import CatalogCard from "../../components/CatalogCard";

function importAll(r) {
  return r.keys().map(r);
}

const catalogLogos = importAll(
  require.context(
    "../../assist/images/catalog-images",
    false,
    /\.(png|jpe?g|svg)$/
  )
);

const titles = [
  "M v B",
  "Qu tng",
  "in Gia dng",
  "in thoi v ph kin",
  "Nh sch online",
  " t, xe my, xe p",
  "Thi trang tr em",
  "Giy dp n",
  "Thi trang nam",
  "Th thao & Du lch",
  "Giy dp nam",
  "Thi trang n",
  "Thit b in gia dng",
  "ng h",
  "Ph kin thi trang",
  "Thit b in t",
  "Voucher",
  "M phm & Lm p",
  "My tnh & Laptop",
  "Bch ha online",
  "Chm sc nh ca",
  " n th cng",
  " chi",
  "My nh",
  "Ti v",
];

function convertData() {
  let result = [];
  for (var key in titles) {
    result.push({
      src: catalogLogos[key],
      title: titles[key],
    });
  }
  return result;
}

export default function Catalog(props) {
  const refEl = useRef(null);

  const catalogList = convertData();
  return (
    <div className="catalog-main">
      <div className="catalog-main__title">
        <h3>DANH MC</h3>
      </div>
      <div ref={refEl} className="catalog-main__catalog-list">
        <div className="catalog-main__catalog-list--top">
          {catalogList.slice(0, 13).map((item, index) => {
            return (
              <div key={index}>
                <CatalogCard
                  width="10.3em"
                  height="13em"
                  src={item.src}
                  alt={item.src}
                  title={item.title}
                />
              </div>
            );
          })}
        </div>
        <div className="catalog-main__catalog-list--bottom">
          {catalogList.slice(13).map((item, index) => {
            return (
              <div key={index}>
                <CatalogCard
                  width="10.3em"
                  height="13em"
                  src={item.src}
                  alt={item.src}
                  title={item.title}
                />
              </div>
            );
          })}
        </div>
      </div>
      <LeftCircleOutlined
        className="catalog-main__icon-scroll-left"
        onClick={() => {
          refEl.current.scrollTo({ left: 0, behavior: "smooth" });
        }}
      />
      <RightCircleOutlined
        className="catalog-main__icon-scroll-right"
        onClick={() => {
          refEl.current.scrollTo({ left: 1000, behavior: "smooth" });
        }}
      />
    </div>
  );
}
